@use '../../../variables.sass' as v

.input-wrap
  display: flex
  flex-direction: column
  margin-bottom: 3rem
  @media screen and (max-width: 1000px)
    margin-bottom: 1rem
.error-text
  height: 22px
  color: v.$color-error
  transform: translateY(-20px)
  &::before
    content: url("data:image/png;base64,")
    position: absolute
    bottom: 120%
    right: 0
    display: block
  &:not(:empty)::before
    opacity: 1
  &:empty::before
    opacity: 0
